
@import "../../styles/variables";

article {
    h2 {
        margin-top: 1vh;
        margin-bottom: 1vh;
        padding-bottom: 1vh;
        font-weight: 600;
        border-bottom: 4px solid;
        transition: $transition;
        user-select: none;

    &:hover {
        transform: translateY(-3px);
        transition: $transition;
    }
    }
    div {
        margin-bottom: 3vh;
    }
}